/* SWIPPER */

.swiper-container {
  width: 100%;
  height: 15rem;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  flex-direction: column;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper-button-next,
.swiper-button-prev {
  margin-top: 5rem !important;
  color: #99b7ae !important;
}

.swiper-pagination-bullet-active {
  background-color: #99b7ae !important;
}

/* COOKIES */

.cookie__modal {
  position: fixed;
