/*CAROUSEL*/
.carousel {
  position: relative;
  margin-top: 100px;
  margin-bottom: 52px;
}
.carousel-indicators {
  /* display: none !important; */
}
.carousel-inner {
  display: inline-flex !important;
  width: 100%;
  position: relative;
  width: 100%;
  overflow: hidden;
}
.carousel-item {
  text-align: center !important;
  width: 100% !important;
  position: relative;
  display: none;
  float: left;
  width: 100%;
  margin-right: -100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transition: transform 0.6s ease-in-out;
}
.carousel__div {
  display: flex;
}
.carousel__opinion {
  margin-right: 20px;
}
.carousel-inner .active {
  display: block !important;
}
.carousel-control-next {
  float: right;
  display: none;
}
.carousel-control-prev {
  float: left;
  display: none;
}
.carousel-control-next-icon {
  background-image: url(./assets/Flecha\ Lat.png);
  display: inline-block;
  width: 20px;
  height: 20px;
  background-size: contain;
  background-repeat: no-repeat;
}
.carousel-control-prev-icon {
  background-image: url(./assets/flechaIzquierda.png);
  display: inline-block;
  width: 20px;
  height: 20px;
  background-size: contain;
  background-repeat: no-repeat;
}
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

